	import {  
        mapMutations  
    } from 'vuex';
    export default {
        data() {
            return {
				showTxt:'获取动态码',
				countdown: 60,
				cTimer: null,
				phoneNumber:'',
				smscode:'',
				invitecode:'',
				isagree:false,
				pwd:'',
				pwd2:'',
                SessionKey: '',
                unionid: '',
                nickName: null,
                avatarUrl: null,
                isCanUse: uni.getStorageSync('isCanUse')||true,//默认为true
				isBind:false
            };
        },
        methods: {
			...mapMutations(['setToken','setUserInfo']), 
			toShow(t){
				if(t==='1'){
					this.clearTimer();
					this.showTxt ='获取动态码';
					this.smscode='';
					this.isBind=true;
				}else if(t==='0'){
					this.clearTimer();
					this.showTxt ='获取动态码';
					this.smscode='';
					this.isBind=false;
				}
			},
			
　　　　　　//登录
            login() {
                let _this = this;
                uni.showLoading({
                    title: '登录中...'
                });
             
               // 1.wx获取登录用户code
                uni.login({
                    provider: 'weixin',
                    success: function(loginRes) {
                        let code = loginRes.code;
                   
						_this.$Request.post('connect/wxlogin',{code:code}).then(res => {
							if (res.code == 200) {
								if(res.datas.state=='10'){
									_this.isCanUse = false;
									_this.SessionKey = res.datas.SessionKey;
									_this.unionid = res.datas.unionid;
								}else{
									console.log(res);
									_this.setToken(res.datas.token);
									_this.setUserInfo(res.datas);
									uni.switchTab({
										url:"/pages/member/home", 
										success (_res) { 
												let page = getCurrentPages().pop(); 
												if (page == undefined || page == null) { 
												  return ;
												 } 
												 page.onLoad() ;
										}
									});
								}
								uni.hideLoading();
							}
							else{
								uni.hideLoading();
								uni.showToast({
									icon: 'none',
									title:res.datas.error,
									duration:2000
								});
							}
						});
                     
                    },
                });
            },

			getPhoneNumber(e) {
				let _this = this;
				console.log(e.detail.errMsg);
				console.log(e.detail.iv);
				console.log(e.detail.encryptedData);
				_this.$Request.post('connect/wxminphone',{iv:e.detail.iv,epdata:e.detail.encryptedData,skey:this.skey}).then(res => {
					if (res.code == 200) {
						if(res.datas.state=='10'){
							_this.isCanUse = false;
						}else{
							console.log(res);
							_this.setToken(res.datas.token);
							_this.setUserInfo(res.datas);
							uni.switchTab({
								url:"/pages/member/home", 
								success (res) { 
										let page = getCurrentPages().pop(); 
										if (page == undefined || page == null) { 
										  return ;
										 } 
										 page.onLoad() ;
								}
							});
						}
						uni.hideLoading();
					}
					else{
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
					}
				});
			},
			toSendSms() {
				var _this = this;
				if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))){ 
					uni.showToast({title: '请填写正确手机号码',icon:"none"});
					return false; 
				}
				
				_this.$Request.post('connect/checkmob',{phone:_this.phoneNumber}).then(res => {
					if (res.code == 200&&res.datas=='1') {
						_this.$Request.post('connect/get_sms_captcha',{phone:_this.phoneNumber,type:1}).then(e => {
							if (e.code == 200) {
								console.log(e);
								_this.startTimer();
								uni.showToast({title: '验证码已发送',icon:"none",duration:2000});
							}
							else{
								uni.showToast({
									icon: 'none',
									title:e.datas.error,
									duration:2000
								});
								
							}
						});
					}
					else{
						uni.showToast({
							icon: 'none',
							title:'手机号码已存在',
							duration:2000
						});
						
					}
				});
				
				
				
			},
			startTimer: function() {
				var _this = this;
				if (_this.cTimer == null) {
					_this.cTimer = setInterval(function() {
						_this.countdown--;
						if (_this.countdown == 0) {
							_this.clearTimer();
						}
					}, 1000)
				}
			},
			clearTimer: function() {
				var _this = this;
				clearInterval(_this.cTimer);
				_this.cTimer = null;
				_this.countdown = 60;
				_this.showTxt='重新发送';
			},
			checkboxChange(e){
				console.log('--------------');
				console.log(e.detail.value[0]);
				//let _isg= e.detail.value[0];
				if(e.detail.value.length>0)
				{
					this.isagree = true;
				}
				 console.log(this.isagree);
			},
			
			checkpwd1(e){
				
				let _pwd1= e.detail.value;
				if(!(/^(?=.*?[a-z)(?=.*>[A-Z])(?=.*?[0-9])[a-zA_Z0-9]{6,10}$/.test(_pwd1)) ){ 
					uni.showToast({title: '密码必须大于6位包含数字和字母',icon:"none"});
					return false; 
				}
			},
			checkpwd2(e){
				if(this.pwd!=this.pwd2 ){ 
					uni.showToast({title: '两次输入的密码不一致',icon:"none"});
					return false; 
				}
			}, 
			nextReg() {  
				var _this =this;
				if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))){ 
					uni.showToast({title: '请填写正确手机号码',icon:"none"});
					return false; 
				}
				
				if(!(/^[0-9]*$/.test(this.smscode)) ||this.smscode.length!=6){ 
					uni.showToast({title: '请填写正确的验证码',icon:"none"});
					return false; 
				}
				
				if(!(/^[A-Za-z0-9]+$/.test(this.invitecode)) ||this.invitecode.length<3){ 
					uni.showToast({title: '请填写正确的邀请码',icon:"none"});
					return false; 
				}
				
				if(!(/^(?=.*?[a-z)(?=.*>[A-Z])(?=.*?[0-9])[a-zA_Z0-9]{6,10}$/.test(this.pwd)) ){ 
					uni.showToast({title: '密码必须大于6位包含数字和字母',icon:"none"});
					return false; 
				}
				if(this.pwd!=this.pwd2 ){ 
					uni.showToast({title: '两次输入的密码不一致',icon:"none"});
					return false; 
				}
				
				if(!this.isagree){uni.showToast({title: '请选择同意注册协议',icon:"none",duration:2000}); return false;}
				
			   uni.showLoading({title: '正在处理中...'});
				_this.$Request.post('connect/check_sms_invite',{phone:_this.phoneNumber,yzcode:_this.smscode,invcode:_this.invitecode,type:1}).then(res => {
					if (res.code == 200) {
						console.log(res);
						//_this.showNext=true;
						let postData={phone:_this.phoneNumber,yzcode:_this.smscode,invcode:_this.invitecode,type:1,
								password:_this.pwd,password_confirm:_this.pwd2,unionid:_this.unionid,nickname:_this.nickName};
						_this.$Request.post('connect/wxmini_register',postData).then(res => {
							if (res.code == 200) {
								console.log(res);
								_this.setToken(res.datas.token);
								_this.setUserInfo(res.datas);
						uni.showToast({
							icon: 'none',
							title:'恭喜您！微信账号绑定成功',
							duration:2000,
							success: function () {
								setTimeout(function () {
										uni.switchTab({
											url:"/pages/member/home", 
											success (res) { 
												let page = getCurrentPages().pop(); 
												if (page == undefined || page == null) { 
												  return ;
												 } 
												 page.onLoad() ;
											}
										});
									 }, 2000);
								}
						});
								
							}
							else{
								uni.hideLoading();
								uni.showToast({
									icon: 'none',
									title:res.datas.error,
									duration:2000
								});
								
							}
						});
					}
					else{
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						
					}
				});
			}, 
			nextBind() {  
				var _this =this;
				if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))){ 
					uni.showToast({title: '请填写正确手机号码',icon:"none"});
					return false; 
				}
				
				if(!(/^[0-9]*$/.test(this.smscode)) ||this.smscode.length!=6){ 
					uni.showToast({title: '请填写正确的验证码',icon:"none"});
					return false; 
				}
				if(_this.unionid==''){ 
					uni.showToast({title: '登陆参数错误！',icon:"none"});
					return false; 
				}
				
				
			   uni.showLoading({title: '正在处理中...'});
				
				let postData={phone:_this.phoneNumber,yzcode:_this.smscode,type:2,unionid:_this.unionid};
				_this.$Request.post('connect/wxbindMember',postData).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.setToken(res.datas.token);
						_this.setUserInfo(res.datas);
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title:'恭喜您！微信账号绑定成功',
							duration:2000,
							success: function () {
								setTimeout(function () {
										uni.switchTab({
											url:"/pages/member/home", 
											success (res) { 
												let page = getCurrentPages().pop(); 
												if (page == undefined || page == null) { 
												  return ;
												 } 
												 page.onLoad() ;
											}
										});
									 }, 2000);
								}
						});
						
						
						
					}
					else{
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						
					}
				});
			},
			toSendSms2() {
				var _this = this;
				if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))){ 
					uni.showToast({title: '请填写正确手机号码',icon:"none"});
					return false; 
				}
				
				_this.$Request.post('connect/get_sms_captcha',{phone:_this.phoneNumber,type:2}).then(e => {
					if (e.code == 200) {
						console.log(e);
						_this.startTimer();
						uni.showToast({title: '验证码已发送',icon:"none",duration:2000});
					}
					else{
						uni.showToast({
							icon: 'none',
							title:e.datas.error,
							duration:2000
						});
						
					}
				});
					
				
				
				
			},
			
        },
        onLoad() {//默认加载
            this.login();
        },
			
    }
